.calendar {
    display: block;
    font-size: 0.765625rem;
    border: 1px solid #e6e8e9;
    border-radius: 3px;
}

.calendar-nav {
    display: flex;
    align-items: center;
}

.calendar-title {
    flex: 1;
    text-align: center;
}

.calendar-body,
