    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    padding: .5rem 0;
}

.calendar-header {
    color: #656d77;
}

.calendar-date {
    flex: 0 0 14.2857143%;
    max-width: 14.2857143%;
    padding: .2rem;
    text-align: center;
    border: 0;
}

.calendar-date.prev-month,
.calendar-date.next-month {
    opacity: .25;
}

.calendar-date .date-item {
    position: relative;
    display: inline-block;
    width: 1.4rem;
    height: 1.4rem;
    line-height: 1.4rem;
    color: #66758c;
    text-align: center;
    text-decoration: none;
    white-space: nowrap;
    vertical-align: middle;
    cursor: pointer;
    background: 0 0;
    border: 1px solid transparent;
    border-radius: 50%;
    outline: 0;
    transition: background .3s, border .3s, box-shadow .32s, color .3s;
}

.calendar-date .date-item:hover {
    color: #206bc4;
    text-decoration: none;
    background: #fefeff;
    border-color: #e6e8e9;
}

.calendar-date .date-today {
    color: #206bc4;
    border-color: #e6e8e9;
}

.calendar-range {
    position: relative;
}

.calendar-range:before {
    position: absolute;
    top: 50%;
    right: 0;
    left: 0;
    height: 1.4rem;
    content: "";
    background: rgba(32, 107, 196, 0.1);
    transform: translateY(-50%);
}

.calendar-range.range-start .date-item,
.calendar-range.range-end .date-item {
    color: #ffffff;
    background: #206bc4;
    border-color: #206bc4;
}

.calendar-range.range-start:before {
    left: 50%;
}

.calendar-range.range-end:before {
    right: 50%;
}

